import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {
  img_URL,
  HotelRoom,
  TrainPrice,
  TrainBerth,
} from '../../shared/Server';
import { MatCarousel, MatCarouselComponent } from 'ng-mat-carousel';
import { GetplanService } from 'src/app/services/getplan.service';

@Component({
  selector: 'app-knowmore',
  templateUrl: './knowmore.component.html',
  styleUrls: ['./knowmore.component.css'],
})
export class KnowmoreComponent implements OnInit {
  package: any;
  realthing: any;
  imgUrl = img_URL;
  currentUrl: string = '';
  hotelRoom = new HotelRoom(); //select the hotel room and price
  slides: object[] = [];
  trainPrice1 = new TrainPrice(); // display options to select the available berths and the price
  trainPrice2 = new TrainPrice();
  trainBerth1 = new TrainBerth(); //check which berths are available
  trainBerth2 = new TrainBerth();

  /**
   * the getdetails fucntion returns object of type
   * [{name:,packages:[{},{}], type: etc},{....}, {....}]
   */

  constructor(
    private route: ActivatedRoute,
    private getplanservice: GetplanService,
    private router: Router
  ) {}

  index: any;
  type: any;
  name: any;
  days: any;

  ngOnInit(): void {
    this.package = this.getplanservice.getDetails();
    this.index = this.route.snapshot.params.id;
    this.type = this.route.snapshot.params.type;
    this.name = this.route.snapshot.params.name;
    this.days = this.package[this.index].days;
    console.log(this.package[this.index]);

    //get the current package  using th
    this.realthing = this.getplanservice.getSelectedPackage(
      this.index,
      this.type,
      this.name,
      this.package
    );
    this.hotelRoom.price =
      (this.realthing.hotel.Price['AC room'] * this.days) / 2;

    for (var i = 1; i <= 2; i++) {
      this.slides.push({
        image:
          img_URL +
          this.realthing.hotel.Locn +
          '/' +
          this.realthing.hotel.Name +
          '/' +
          i +
          '.jpg',
      });
    }

    if (this.type == 'Train') {
      var temp = this.realthing.train;
      if (temp['1AC'] > 0) this.trainBerth1['1AC'] = true;
      if (temp['2AC'] > 0) this.trainBerth1['2AC'] = true;
      if (temp['3AC'] > 0) this.trainBerth1['3AC'] = true;
      if (temp['1S'] > 0) this.trainBerth1['1S'] = true;
      if (temp['2S'] > 0) this.trainBerth1['2S'] = true;
      if (temp['Sleeper'] > 0) this.trainBerth1['Sleeper'] = true;
      if (this.realthing.trainback != undefined) {
        var temp = this.realthing.trainback;
        if (temp['1AC'] > 0) this.trainBerth2['1AC'] = true;
        if (temp['2AC'] > 0) this.trainBerth2['2AC'] = true;
        if (temp['3AC'] > 0) this.trainBerth2['3AC'] = true;
        if (temp['1S'] > 0) this.trainBerth2['1S'] = true;
        if (temp['2S'] > 0) this.trainBerth2['2S'] = true;
        if (temp['Sleeper'] > 0) this.trainBerth2['Sleeper'] = true;
      }
    }
  }

  changeRoom(id: string) {
    this.hotelRoom.ac = false;
    this.hotelRoom.nac = false;
    this.hotelRoom.premium = false;
    this.hotelRoom.deluxe = false;
    console.log(this.days);
    if (id == 'ac') {
      this.hotelRoom.ac = true;
      this.hotelRoom.price =
        (this.realthing.hotel.Price['AC room'] * this.days) / 2;
    } else if (id == 'nac') {
      this.hotelRoom.nac = true;
      this.hotelRoom.price =
        (this.realthing.hotel.Price['Non AC room'] * this.days) / 2;
    } else if (id == 'premium') {
      this.hotelRoom.premium = true;
      this.hotelRoom.price =
        (this.realthing.hotel.Price['Premium Suite'] * this.days) / 2;
    } else if (id == 'deluxe') {
      this.hotelRoom.deluxe = true;
      this.hotelRoom.price =
        (this.realthing.hotel.Price['Delux room'] * this.days) / 2;
    }
  }

  changeTrainPrice(type: string) {
    this.trainPrice1 = new TrainPrice();
    console.log(this.realthing.train.Price);
    if (type == '1AC') {
      this.trainPrice1['1AC'] = true;
      this.trainPrice1.price = this.realthing.train.Price[type];
    }
    if (type == '2AC') {
      this.trainPrice1['2AC'] = true;
      this.trainPrice1.price = this.realthing.train.Price[type];
    }
    if (type == '3AC') {
      this.trainPrice1['3AC'] = true;
      this.trainPrice1.price = this.realthing.train.Price[type];
    }
    if (type == '1S') {
      this.trainPrice1['1S'] = true;
      this.trainPrice1.price = this.realthing.train.Price[type];
    }
    if (type == '2S') {
      this.trainPrice1['2S'] = true;
      this.trainPrice1.price = this.realthing.train.Price[type];
    }
    if (type == 'Sleeper') {
      this.trainPrice1.Sleeper = true;
      this.trainPrice1.price = this.realthing.train.Price[type];
    }
  }

  changeRetTrainPrice(type: string) {
    this.trainPrice2 = new TrainPrice();
    if (type == '1AC') {
      this.trainPrice2['1AC'] = true;
      this.trainPrice2.price = this.realthing.trainback.Price[type];
    }
    if (type == '2AC') {
      this.trainPrice2['2AC'] = true;
      this.trainPrice2.price = this.realthing.trainback.Price[type];
    }
    if (type == '3AC') {
      this.trainPrice2['3AC'] = true;
      this.trainPrice2.price = this.realthing.trainback.Price[type];
    }
    if (type == '1S') {
      this.trainPrice2['1S'] = true;
      this.trainPrice2.price = this.realthing.trainback.Price[type];
    }
    if (type == '2S') {
      this.trainPrice2['2S'] = true;
      this.trainPrice2.price = this.realthing.trainback.Price[type];
    }
    if (type == 'Sleeper') {
      this.trainPrice2.Sleeper = true;
      this.trainPrice2.price = this.realthing.trainback.Price[type];
    }
  }

  openCustomize() {
    this.router.navigate([
      this.getplanservice.currentUrl +
        '/customize/' +
        this.index +
        '/' +
        this.name +
        '/' +
        this.type,
    ]);
  }
}
