import { Component, DoCheck, Input, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { DomSanitizer } from '@angular/platform-browser';
import { StorageService } from 'src/app/services/storage.service';
import { FilterService } from 'src/app/services/filter.service';

import { img_URL } from '../../shared/Server';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';

@Component({
  selector: 'app-displaypackages',
  templateUrl: './displaypackages.component.html',
  styleUrls: ['./displaypackages.component.css'],
})
export class DisplaypackagesComponent implements OnInit, DoCheck {
  @Input() ROUTES: any;
  @ViewChild(MatPaginator) paginator: MatPaginator | undefined;
  imgUrl = img_URL;
  filters: packageFilters[] = [];
  type: string = '';

  constructor(
    private _DomSanitizationService: DomSanitizer,
    private filterservice: FilterService,
    private router: Router,
    private route: ActivatedRoute,
    private storageservice: StorageService
  ) {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.type = event.url.split('/')[1];
      }
    });
  }

  ngDoCheck(): void {
    var n = this.ROUTES.length;
    var m = this.filters.length;
    if (n === m) {
      for (var i = 0; i < n; i++) {
        if (this.ROUTES[i].NAME !== this.filters[i].name) {
          this.ngOnInit();
          break;
        } else {
          for (var j = 0; j < this.ROUTES[i].packages.length; j++) {
            if (
              this.ROUTES[i].packages[j].total !==
              this.filters[i].packages[j].total
            ) {
              this.ngOnInit();
              break;
            }
          }
        }
      }
    } else {
      this.ngOnInit();
    }
  }

  ngOnInit(): void {
    if (this.ROUTES.length > 0) {
      this.filters.length = 0;
      for (var i = 0; i < this.ROUTES.length; i++) {
        this.filters.push({
          flight: true,
          train: true,
          bus: true,
          price: false,
          distance: false,
          time: false,
          nac: false,
          ac: false,
          deluxe: false,
          premium: false,
          name: this.ROUTES[i].NAME,
          isReturn: this.ROUTES[i].checkout,
          packages: JSON.parse(JSON.stringify(this.ROUTES[i].packages)),
          dispackages: JSON.parse(JSON.stringify(this.ROUTES[i].packages)),
        });
        this.applyFilter(i);
      }
    }
  }

  applyFilter(index: number) {
    this.filters[index].dispackages.length = 0;
    this.filters[index].dispackages = this.filterservice.Filter(
      this.filters[index]
    );
  }

  openKnowMore(i: number, packname: string, type: string) {
    window.open(
      'http://localhost:4200/' +
        this.type +
        '/knowmore/' +
        i +
        '/' +
        packname +
        '/' +
        type,
      '_blank'
    );
  }

  openCustomize(i: number, packname: string, type: string) {
    window.open(
      'http://localhost:4200/' +
        this.type +
        '/customize/' +
        i +
        '/' +
        packname +
        '/' +
        type,
      '_blank'
    );
  }
}

export class packageFilters {
  flight: boolean = true;
  train: boolean = false;
  bus: boolean = false;
  price: boolean = false;
  distance: boolean = false;
  time: boolean = false;
  nac: boolean = false;
  ac: boolean = false;
  deluxe: boolean = false;
  premium: boolean = false;
  name: String = '';
  isReturn: string = '';
  dispackages: any = [];
  packages: any = [];
}
