import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { DiscoverComponent } from './components/discover/discover.component';
import { BusinessComponent } from './components/business/business.component';
import { EventComponent } from './components/event/event.component';
import { RegisterComponent } from './components/register/register.component';
import { LoginComponent } from './components/login/login.component';
import { LeisureComponent } from './components/leisure/leisure.component';

import { KnowmoreComponent } from './components/knowmore/knowmore.component';
import { CustomizepackageComponent } from './components/customizepackage/customizepackage.component';
import { BookpackageComponent } from './components/bookpackage/bookpackage.component';
import { PassengerdetailsComponent } from './components/passengerdetails/passengerdetails.component';

import { AuthGuard } from './auth/auth.guard';
import { UnauthGuard } from './auth/unauth.guard';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'discover', component: DiscoverComponent },
  { path: 'business', component: BusinessComponent },
  /*  { path: 'events', component: EventComponent }, */
  {
    path: 'register',
    canActivate: [UnauthGuard],
    component: RegisterComponent,
  },
  { path: 'login', canActivate: [UnauthGuard], component: LoginComponent },
  { path: 'leisure', component: LeisureComponent },
  { path: 'leisure/knowmore/:id/:name/:type', component: KnowmoreComponent },
  { path: 'events/knowmore/:id/:name/:type', component: KnowmoreComponent },
  { path: 'business/knowmore/:id/:name/:type', component: KnowmoreComponent },
  {
    path: 'leisure/bookpackage',
    canActivate: [AuthGuard],
    component: PassengerdetailsComponent,
  },
  /* {
    path: 'events/bookpackage',
    canActivate: [AuthGuard],
    component: PassengerdetailsComponent,
  }, */
  {
    path: 'business/bookpackage',
    canActivate: [AuthGuard],
    component: PassengerdetailsComponent,
  },
  {
    path: 'payment',
    canActivate: [AuthGuard],
    component: BookpackageComponent,
  },
  {
    path: 'leisure/customize/:id/:name/:type',
    component: CustomizepackageComponent,
  },
  /* {
    path: 'events/customize/:id/:name/:type',
    component: CustomizepackageComponent,
  }, */
  {
    path: 'business/customize/:id/:name/:type',
    component: CustomizepackageComponent,
  },
  {
    path: 'user',
    canActivate: [AuthGuard],
    loadChildren: () =>
      import('./modules/user/user.module').then((m) => m.UserModule),
  },
  {
    path: '**',
    redirectTo: '/home',
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
