  @HostListener('window:scroll', ['$event'])
  checkScroll() {
    const componentPosition = this.el.nativeElement.offsetTop;
    const discover = document.getElementById('discover')!.offsetTop;
    const scrollPosition = window.pageYOffset;

    if (scrollPosition >= componentPosition) {
      this.ShowHidediv.usp = 'show';
      if (scrollPosition >= discover - 600) this.ShowHidediv.discover = 'show';
      if (scrollPosition >= discover + 600) this.ShowHidediv.contactus = 'show';
      if (scrollPosition >= discover - 300) this.ShowHidediv.offers = 'show';
    } /* else {
      this.ShowHidediv.usp = 'hide';
      this.ShowHidediv.discover = 'hide';
      this.ShowHidediv.contactus = 'hide';
      this.ShowHidediv.offers = 'hide';
    } */
  }

