import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { slideDown, slideLeft } from 'src/app/shared/animations';
import { Router } from '@angular/router';
import { AuthService } from '../../auth/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
  animations: [slideDown, slideLeft],
})
export class RegisterComponent implements OnInit {
  private user: any;
  private loggedIn: any;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private loginservice: AuthService
  ) {}

  ngOnInit() {}

  info = this.fb.group({
    name: ['', Validators.required],
    surname: ['', Validators.required],
    email: ['', [Validators.required, Validators.email]],
    mob: [
      '',
      [Validators.required, Validators.maxLength(10), Validators.minLength(10)],
    ],
    password: ['', Validators.required],
    confirm: ['', Validators.required],
  });

  onsubmit() {
    console.log(this.info.value);
    this.loginservice.register(this.info.value).subscribe(
      () => {
        this.router.navigate(['/user']);
      },
      (errorResponse) => {
        console.log(errorResponse);
      }
    );
  }

  signInWithGoogle() {
    this.loginservice.googleLogin().subscribe(
      (data) => {
        console.log(data);
        this.router.navigate(['/user']);
      },
      (err) => console.log(err)
    );
  }
}
