import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { catchError } from 'rxjs/operators';
import { ProcessHTTPMsgService } from 'src/app/services/process-httpmsg.service';

@Injectable({
  providedIn: 'root',
})
export class FetchpackagesService {
  constructor(
    private http: HttpClient,
    private processHTTPMsgService: ProcessHTTPMsgService
  ) {}

  userPackages(userEmail: string) {
    return this.http
      .post(
        'http://localhost:3000/packages/fetch',
        { id: userEmail },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  savepackage(id: string, pack: any) {
    return this.http
      .post(
        'http://localhost:3000/packages/save',
        {
          id: id,
          package: JSON.stringify(pack),
          date: new Date(),
          name: pack.name,
          type: pack.packageType,
        },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }
}
