import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';

export let fade = trigger('fade', [
  state('void', style({ opacity: 0 })),
  transition(':enter,:leave', [animate(500)]),
]);

export let slideDown = trigger('slideDown', [
  state(
    'show',
    style({
      opacity: 1,
      transform: 'translateY(0)',
    })
  ),
  state('void', style({ opacity: 0, transform: 'translateY(-100%)' })),
  state(
    'hide',
    style({
      opacity: 0,
      transform: 'translateY(-100%)',
    })
  ),
  transition('show <=> hide', animate('700ms ease-out')),
  transition(':enter,:leave', animate('700ms ease-out')),
]);

export let slideLeft = trigger('slideLeft', [
  state(
    'show',
    style({
      opacity: 1,
      transform: 'translateX(0)',
    })
  ),
  state('void', style({ opacity: 0, transform: 'translateX(-100%)' })),
  state(
    'hide',
    style({
      opacity: 0,
      transform: 'translateX(-100%)',
    })
  ),
  transition('show <=> hide', animate('700ms ease-out')),
  transition(':enter,:leave', animate('700ms ease-out')),
]);

export let slideUp = trigger('slideUp', [
  state(
    'show',
    style({
      opacity: 1,
      transform: 'translateY(0)',
    })
  ),
  state(
    'hide',
    style({
      opacity: 0,
      transform: 'translateY(+100%)',
    })
  ),
  transition('show <=> hide', animate('700ms ease-out')),
]);

export let navbar = trigger('navbar', [
  state(
    'top',
    style({
      opacity: 1,
      background: 'white',
      color: 'royalblue',
    })
  ),
  state(
    'middle',
    style({
      opacity: 1,
      background: 'white',
      boxShadow: '0px 1px 3px -1px',
      color: 'royalblue',
    })
  ),
  transition('top <=> middle', animate('100ms ease-out')),
]);
