import { Injectable } from '@angular/core';
import { map, catchError } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ProcessHTTPMsgService } from './process-httpmsg.service';
import { NavigationEnd, Router } from '@angular/router';
import { StorageService } from './storage.service';

@Injectable({
  providedIn: 'root',
})
export class GetplanService {
  constructor(
    private http: HttpClient,
    private processHTTPMsgService: ProcessHTTPMsgService,
    private router: Router,
    private storageservice: StorageService
  ) {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.currentUrl = event.url.split('/')[1];
      }
    });
  }

  currentUrl: string = '';

  //will fetch from the api plans from src to dest
  trynow(options: any) {
    if (options.source) var src = options.source;
    else var src: any = 'Mumbai';
    if (options.persons) var people = options.persons;
    else var people: any = 1;
    var dst = options.destination;
    var depDate = options.startDate;
    var endDate = options.endDate;
    return this.http
      .post(
        'http://localhost:3000/getpackages/',
        {
          src: src,
          dest: dst,
          checkin: depDate,
          checkout: endDate,
          persons: people,
        },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  //will fetch rounkd trip of the selected src to dest
  getRoundPackages(options: any) {
    if (options.source) var src = options.source;
    else var src: any = 'Mumbai';
    if (options.persons) var people = options.persons;
    else var people: any = 1;
    var dst = options.destination;
    var depDate = options.startDate;
    var retDate = options.endDate;
    return this.http
      .post(
        'http://localhost:3000/getpackages/round',
        {
          src: src,
          dest: dst,
          checkin: depDate,
          checkout: retDate,
          persons: people,
        },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getOffers() {
    return this.http
      .post('http://localhost:3000/offers', {})
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getSelectedPackage(index: any, type: any, name: any, pack: any) {
    var realthing: any;
    for (var i = 0; i < pack[index].packages.length; i++) {
      if (
        pack[index].packages[i].type == type &&
        pack[index].packages[i].name == name
      ) {
        realthing = pack[index].packages[i];
        return realthing;
      }
    }
    return undefined;
  }

  getDetails() {
    var type = this.currentUrl;
    if (type == 'leisure') return this.storageservice.sessionGet('Leisure');
    else if (type == 'events') return this.storageservice.sessionGet('Event');
    else if (type == 'business')
      return this.storageservice.sessionGet('Business');
    else return undefined;
  }

  getFlights(
    source: string,
    destination: string,
    checkin: string,
    persons: any
  ) {
    return this.http
      .post(
        'http://localhost:3000/getpackages/flights',
        { src: source, dest: destination, checkin: checkin, persons: persons },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getHotels(dest: string) {
    return this.http
      .post(
        'http://localhost:3000/getpackages/hotels',
        { dest: dest },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getTrains(
    source: string,
    destination: string,
    checkin: string,
    persons: any
  ) {
    return this.http
      .post(
        'http://localhost:3000/getpackages/trains',
        { src: source, dest: destination, checkin: checkin, persons: persons },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getBus(source: string, destination: string, checkin: string, persons: any) {
    return this.http
      .post(
        'http://localhost:3000/getpackages/bus',
        { src: source, dest: destination, checkin: checkin, persons: persons },
        {
          headers: new HttpHeaders({
            'Content-Type': 'application/json',
          }),
        }
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }
}
