import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { StorageService } from 'src/app/services/storage.service';
import { cities } from 'src/app/shared/TestCities';
import { GetplanService } from '../../services/getplan.service';
import { fade, slideDown, slideLeft, slideUp } from 'src/app/shared/animations';

@Component({
  selector: 'app-leisure',
  templateUrl: './leisure.component.html',
  styleUrls: ['./leisure.component.css'],
  animations: [fade],
})
export class LeisureComponent implements OnInit {
  constructor(
    private fb: FormBuilder,
    private getplanservice: GetplanService,
    private storageservice: StorageService
  ) {}

  ngOnInit(): void {
    if (this.storageservice.sessionGet('leisureForm') != null) {
      this.explore.patchValue(this.storageservice.sessionGet('leisureForm'));
      this.ROUTES = this.storageservice.sessionGet('Leisure');
    }
  }
  explore = this.fb.group({
    destination: ['', Validators.required],
    startDate: ['', Validators.required],
    endDate: ['', Validators.required],
    round: ['', Validators.required],
  });

  cities = cities;
  today = new Date();
  ROUTES: object[] = [];
  status = -1;

  getPlans() {
    if (this.ROUTES) this.ROUTES.length = 0;
    this.storageservice.sessionStore('leisureForm', this.explore.value);
    this.status = 0;

    if (this.explore.value.round == 'one-way') {
      this.getplanservice.trynow(this.explore.value).subscribe(
        (data) => {
          this.ROUTES.push(data);
          this.storageservice.sessionStore('Leisure', this.ROUTES);
          this.status = -1;
        },

        (err) => console.log('some error')
      );
    } else {
      this.getplanservice.getRoundPackages(this.explore.value).subscribe(
        (data) => {
          this.ROUTES.push(data);
          this.storageservice.sessionStore('Leisure', this.ROUTES);
          this.status = -1;
        },
        (err) => console.log('some error')
      );
    }
  }
}
