import { Injectable } from '@angular/core';
import { map, catchError } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ProcessHTTPMsgService } from './process-httpmsg.service';

@Injectable({
  providedIn: 'root',
})
export class GetRouteService {
  constructor(
    private http: HttpClient,
    private processHTTPMsgService: ProcessHTTPMsgService
  ) {}

  getLatLong(source: string): Observable<any> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http
      .post(
        'http://localhost:3000/flights/src/latlong',
        {
          src: source,
        },
        httpOptions
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }



  getNearestAirport(latlon: any): Observable<any> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http
      .post(
        'http://localhost:3000/flights/airport',
        {
          lat: latlon.lat,
          lon: latlon.lon,
        },
        httpOptions
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getFlightsPrice(airports: any): Observable<any> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http
      .post(
        'http://localhost:3000/flights/getprices',
        {
          src: airports.src,
          dest: airports.dest,
          date: airports.date,
        },
        httpOptions
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }


  getAllinOne(source:any, destination: any, persons:any, date:any)
  {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http
      .post(
        'http://localhost:3000/allinone',
        {
          src: source,
          dest: destination,
          persons: persons,
          date: date
        },
        httpOptions
      )
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }
}
