export const img_URL = 'http://localhost:3000/images/Hotels/';

export class HotelRoom {
  ac: boolean;
  nac: boolean;
  premium: boolean;
  deluxe: boolean;
  price: any;

  public constructor() {
    this.ac = true;
    this.nac = false;
    this.premium = false;
    this.deluxe = false;
    this.price = 0;
  }
}

export class TrainPrice {
  '1AC': boolean;
  '2AC': boolean;
  '3AC': boolean;
  '1S': boolean;
  '2S': boolean;
  Sleeper: boolean;
  price: any;

  public constructor() {
    this['1AC'] = false;
    this['2AC'] = false;
    this['3AC'] = false;
    this['1S'] = false;
    this['2S'] = false;
    this.Sleeper = false;
    this.price = 0;
  }
}

export class TrainBerth {
  '1AC': boolean;
  '2AC': boolean;
  '3AC': boolean;
  '1S': boolean;
  '2S': boolean;
  Sleeper: boolean;

  public constructor() {
    this['1AC'] = false;
    this['2AC'] = false;
    this['3AC'] = false;
    this['1S'] = false;
    this['2S'] = false;
    this.Sleeper = false;
  }
}

export class Package {
  Name: string = '';
  hotel: any;
  flight: any;
  train: any;
  bus: any;
  flightback: any;
  busback: any;
  trainback: any;
  price = 0;
  persons: any;
  days: any;
}
