import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, Validators } from '@angular/forms';
import { GetplanService } from 'src/app/services/getplan.service';
import { cities } from '../../shared/TestCities';
import { DomSanitizer } from '@angular/platform-browser';
import { img_URL } from '../../shared/Server';
import { StorageService } from '../../services/storage.service';
import { fade, slideDown, slideLeft, slideUp } from 'src/app/shared/animations';
@Component({
  selector: 'app-event',
  templateUrl: './event.component.html',
  styleUrls: ['./event.component.css'],
  animations: [fade],
})
export class EventComponent implements OnInit {
  constructor(
    private fb: FormBuilder,
    private getplanservice: GetplanService,
    private _DomSanitizationService: DomSanitizer,
    private storageservice: StorageService
  ) {}

  ngOnInit(): void {
    if (this.storageservice.sessionGet('Event') != null) {
      this.ROUTES = this.storageservice.sessionGet('Event');
      var eForm = this.storageservice.sessionGet('eventForm');
      for (var i = 0; i < eForm.routes.length - 1; i++) this.addroutebutton();
      this.eventDetails.patchValue(eForm);
    }
  }

  imgUrl = img_URL;
  cities = cities;
  ROUTES: object[] = [];
  today = new Date();
  div1 = false;
  status = -1;

  eventDetails = this.fb.group({
    name: ['', Validators.required],
    startDate: ['', Validators.required],
    endDate: ['', Validators.required],
    routes: this.fb.array([this.addroute()]),
  });

  addroute() {
    return this.fb.group({
      source: ['', Validators.required],
      destination: ['', Validators.required],
      persons: ['', Validators.required],
    });
  }

  addroutebutton() {
    (<FormArray>this.eventDetails.get('routes')!).push(this.addroute());
  }

  deleteroutebutton(i: number) {
    let routearray = <FormArray>this.eventDetails.get('routes');
    if (routearray.length > 1) routearray.removeAt(i);
  }

  getControls() {
    return (this.eventDetails.get('routes') as FormArray).controls;
  }

  onSubmit() {
    this.ROUTES.length = 0;
    this.storageservice.sessionStore('eventForm', this.eventDetails.value);
    this.eventDetails.controls['name'].disable();

    for (let i of this.eventDetails.value.routes) {
      var k = 0;
      this.status = 0;

      setTimeout(() => {
        this.getplanservice
          .getRoundPackages({
            source: i.source,
            destination: i.destination,
            startDate: this.eventDetails.value.startDate,
            endDate: this.eventDetails.value.endDate,
            persons: i.persons,
          })
          .subscribe((data: any) => {
            this.ROUTES.push(data);
            this.status = -1;
            this.storageservice.sessionStore('Event', this.ROUTES);
          });
      }, 0);
    }
  }
}

export interface FLIGHTS {
  src: string;
  dest: string;
  srclatlon: string;
  destlatlon: string;
  flights: [
    {
      name: string;
      departure: string;
      arrival: string;
      price: string;
      type: string;
    }
  ];
}
