import { Component, OnInit, ViewChild } from '@angular/core';
import { GetRouteService } from '../../services/get-route.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';

@Component({
  selector: 'app-discover',
  templateUrl: './discover.component.html',
  styleUrls: ['./discover.component.css'],
})
export class DiscoverComponent implements OnInit {
  constructor(private getrouteservice: GetRouteService) {}
  // var to get filght data
  flights!: any;
  // for displaying tabel columns
  displayedColumns: string[] = [
    'name',
    'departure',
    'arrival',
    'price',
    'type',
    'book',
  ];

  disHotelCols: string[] = ['name', 'lat', 'lon', 'book'];
  //for displaying progress
  status = 'idle';
  // datasource for table
  dataSource: any;
  hoteldataSource: any;
  @ViewChild(MatPaginator)
  paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  ngOnInit(): void {}

  getAirport(air: any) {
    this.status = 'fetch';
    this.getrouteservice.getFlightsPrice(air).subscribe(
      (res) => {
        this.status = 'done';
        this.flights = res;
        this.dataSource = new MatTableDataSource(this.flights);

        setTimeout(() => {
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        });
      },
      (err) => {
        this.status = 'error';
        console.log(err);
      }
    );
  }

  getHotels(hotels: any) {
    this.hoteldataSource = new MatTableDataSource(hotels);
  }
}
