import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';

import { STATES } from '../../shared/States';
import { CITIES } from '../../shared/Cities';
import { GetRouteService } from '../../services/get-route.service';
import { GethotelsService } from '../../services/gethotels.service';
import { AutocompleteService } from '../../services/autocomplete.service';
import { DETAILS } from '../../shared/details';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
})
export class DashboardComponent implements OnInit {
  constructor(
    private fb: FormBuilder,
    private getrouteservice: GetRouteService,
    private gethotelsservice: GethotelsService,
    private autocomplete: AutocompleteService
  ) {}
  @Output() airports = new EventEmitter<any>();
  @Output() hotels = new EventEmitter<any>();
  airport = { src: '', dest: '', date: '' };
  isEditable = true;
  states = STATES;
  cities = CITIES;
  days = ['1-2 days', '3-4 days', '6-7 days', '7+ days', 'Not decided'];
  ratings = ['1 star', '2 stars', '3 stars', '4 stars', '5 stars'];
  srclatlon = { lat: '', lon: '' };
  destlatlon = { lat: '', lon: '' };

  ngOnInit(): void {}

  details: DETAILS = {
    source: '',
    destination: '',
    roundTrip: '',
    duration: '',
    departureDate: '',
    luxury: '',
  };

  firstFormGroup = this.fb.group({
    source: ['', Validators.required],
    destination: ['', Validators.required],
    ss: [''],
    roundTrip: [''],
  });

  secondFormGroup = this.fb.group({
    duration: ['Not Decided'],
    departureDate: ['', Validators.required],
    luxury: ['3 stars'],
  });

  onSubmit() {
    {
      this.details.source = this.firstFormGroup.value.source;
      this.details.destination = this.firstFormGroup.value.destination;
      this.details.roundTrip = this.firstFormGroup.value.roundTrip;
      this.details.duration = this.secondFormGroup.value.duration;
      this.details.departureDate = this.secondFormGroup.value.departureDate;
      this.details.luxury = this.secondFormGroup.value.luxury;
      this.airport.date = this.details.departureDate;
    }

    //get source airport iata code

    this.getrouteservice.getLatLong(this.details.source).subscribe(
      (res) => {
        this.srclatlon.lat = res.lat;
        this.srclatlon.lon = res.lon;
        this.getrouteservice.getNearestAirport(this.srclatlon).subscribe(
          (res) => {
            console.log('src', res.results[0].iata);
            this.airport.src = res.results[0].iata;
          },
          (err) => console.log(err)
        );
      },
      (err) => console.log(err)
    );

    //get dest airport iata code and hotels
    this.getrouteservice.getLatLong(this.details.destination).subscribe(
      (res) => {
        this.destlatlon.lat = res.lat;
        this.destlatlon.lon = res.lon;
        this.getrouteservice.getNearestAirport(this.destlatlon).subscribe(
          (res) => {
            console.log('dest', res.results[0].iata);
            this.airport.dest = res.results[0].iata;
            this.airports.emit(this.airport);
            this.gethotelsservice.gethotels(this.destlatlon).subscribe(
              (res) => {
                this.hotels.emit(res);
              },
              (err) => console.log(err)
            );
          },
          (err) => console.log(err)
        );
      },
      (err) => console.log(err)
    );
  }

  search(event: any) {
    const searchWord = event.toLowerCase();
    if (searchWord && searchWord.length > 0) {
      console.log(event);
      this.autocomplete.seacrh(searchWord).subscribe(
        (res) => console.log(res),
        (err) => console.log('err')
      );
    }
  }
}
