import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { slideDown } from 'src/app/shared/animations';
import { Router } from '@angular/router';
import { AuthService } from '../../auth/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  animations: [slideDown],
})
export class LoginComponent implements OnInit {
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private loginservice: AuthService
  ) {}

  ngOnInit() {}

  info = this.fb.group({
    email: ['', [Validators.required, Validators.email]],
    password: ['', Validators.required],
  });

  onsubmit() {
    this.loginservice.login(this.info.value).subscribe(
      (token) => {
        if (this.loginservice.redirectUrl != undefined)
          this.router.navigate([this.loginservice.redirectUrl]);
        else this.router.navigate(['/user']);
      },
      (errorResponse) => {
        console.log(errorResponse);
      }
    );
  }

  signInWithGoogle(): void {
    this.loginservice.googleLogin().subscribe(
      (data) => {
        console.log(data);
        this.router.navigate(['/user']);
      },
      (err) => console.log(err)
    );
  }
}
