import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MailService } from '../../services/mail.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AuthService } from '../../auth/auth.service';
import { FetchpackagesService } from '../../modules/user/services/fetchpackages.service';
import { StorageService } from 'src/app/services/storage.service';

@Component({
  selector: 'app-bookpackage',
  templateUrl: './bookpackage.component.html',
  styleUrls: ['./bookpackage.component.css'],
})
export class BookpackageComponent implements OnInit {
  constructor(
    private mailservice: MailService,
    private router: Router,
    private _snackBar: MatSnackBar,
    private authservice: AuthService,
    private storageservice: StorageService,
    private fetchpackageservice: FetchpackagesService
  ) {}

  ngOnInit(): void {}
  sendmail() {
    var user = this.authservice.getUsername();
    var pack = this.storageservice.sessionGet('bookPackage');
    this.fetchpackageservice.savepackage(user.email, pack).subscribe(
      (data) => {
        console.log('success');

        this.storageservice.sessionDelete('bookPackage');
        this.mailservice
          .sendBookingConfirmation({
            name: user.name,
            email: user.email,
          })
          .subscribe(
            (res) => {
              this._snackBar.open('Mail has been sent', 'close', {
                duration: 2000,
              });
              setTimeout(() => {
                this.router.navigate(['/home']);
              }, 2000);
            },
            (err) => {
              this._snackBar.open('Mail sending failed', 'close', {
                duration: 2000,
              });
              console.log(err);
            }
          );
      },
      (err) => {
        console.log('err');
      }
    );
  }
}
