import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, Validators } from '@angular/forms';
import { GetplanService } from 'src/app/services/getplan.service';
import { DomSanitizer } from '@angular/platform-browser';
import { StorageService } from 'src/app/services/storage.service';
import { cities } from 'src/app/shared/TestCities';
import { img_URL } from 'src/app/shared/Server';
import { fade, slideDown, slideLeft, slideUp } from 'src/app/shared/animations';

@Component({
  selector: 'app-business',
  templateUrl: './business.component.html',
  styleUrls: ['./business.component.css'],
  animations: [fade],
})
export class BusinessComponent implements OnInit {
  constructor(
    private fb: FormBuilder,
    private getplanservice: GetplanService,
    private _DomSanitizationService: DomSanitizer,
    private storageservice: StorageService
  ) {}

  imgUrl = img_URL;
  cities = cities;
  ROUTES: object[] = [];
  today = new Date();
  status = -1;

  ngOnInit(): void {
    if (this.storageservice.sessionGet('Business') != null) {
      this.ROUTES = this.storageservice.sessionGet('Business');
      var bForm = this.storageservice.sessionGet('businessForm');
      for (var i = 0; i < bForm.routes.length - 1; i++) this.addroutebutton();
      this.tripDetails.patchValue(bForm);
    }
  }

  tripDetails = this.fb.group({
    name: ['', Validators.required],
    type: ['', Validators.required],
    routes: this.fb.array([this.addroute()]),
  });

  addroute() {
    return this.fb.group({
      source: ['', Validators.required],
      destination: ['', Validators.required],
      persons: ['', Validators.required],
      startDate: ['', Validators.required],
      endDate: ['', Validators.required],
    });
  }

  addroutebutton() {
    (<FormArray>this.tripDetails.get('routes')!).push(this.addroute());
  }

  deleteroutebutton(i: number) {
    let routearray = <FormArray>this.tripDetails.get('routes');
    if (routearray.length > 1) routearray.removeAt(i);
  }

  getControls() {
    return (this.tripDetails.get('routes') as FormArray).controls;
  }

  onSubmit() {
    this.ROUTES.length = 0;

    this.storageservice.sessionStore('businessForm', this.tripDetails.value);
    this.status = 0;

    for (let i of this.tripDetails.value.routes) {
      var k = 0;

      setTimeout(() => {
        this.getplanservice
          .getRoundPackages({
            source: i.source,
            destination: i.destination,
            startDate: i.startDate,
            endDate: i.endDate,
            persons: i.persons,
          })
          .subscribe((data: any) => {
            this.ROUTES.push(data);
            this.status = -1;
            this.storageservice.sessionStore('Business', this.ROUTES);
          });
      }, 0);
    }
  }
}
