import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { SocialAuthService } from 'angularx-social-login';
import { GoogleLoginProvider } from 'angularx-social-login';
import { Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { ProcessHTTPMsgService } from '../services/process-httpmsg.service';
import * as moment from 'moment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { JwtHelperService } from '@auth0/angular-jwt';

const jwt = new JwtHelperService();

class DecodedToken {
  exp!: number;
  username!: string;
}

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  user: any;
  isLoggedIn = false;
  redirectUrl: any;

  private uriseg = 'http://localhost:3000/auth';
  private decodedToken;

  constructor(
    private authService: SocialAuthService,
    private processHTTPMsgService: ProcessHTTPMsgService,
    private router: Router,
    private auth: SocialAuthService,
    private http: HttpClient
  ) {
    this.auth.authState.subscribe((user) => {
      this.user = user;
      this.isLoggedIn = user != null;
    });
    this.decodedToken =
      JSON.parse(localStorage.getItem('auth_meta') || '{}') ||
      new DecodedToken();
  }

  public register(userData: any): Observable<any> {
    const URI = this.uriseg + '/register';
    return this.http.post(URI, userData);
  }

  public login(userData: any): Observable<any> {
    const URI = this.uriseg + '/login';
    return this.http.post(URI, userData).pipe(
      map((token) => {
       /*  console.log(this.saveToken(token), 'service'); */
        return this.saveToken(token);
      })
    );
  }

  private saveToken(token: any): any {
    this.decodedToken = jwt.decodeToken(token);
    localStorage.setItem('auth_tkn', token);
    localStorage.setItem('auth_meta', JSON.stringify(this.decodedToken));
    return token;
  }

  public logout(): void {
    localStorage.removeItem('auth_tkn');
    localStorage.removeItem('auth_meta');

    this.decodedToken = new DecodedToken();
  }

  public isAuthenticated(): Observable<any> {
    return new Observable((observer) => {
      if (moment().isBefore(moment.unix(this.decodedToken.exp)))
        observer.next(true);
      else observer.next(false);
    });
  }

  public isAuthenticatedGuard() {
    return moment().isBefore(moment.unix(this.decodedToken.exp));
  }

  public getUsername(): any {
    return {
      email: this.decodedToken.userId,
      name: this.decodedToken.username,
    };
  }

  googleLogin(): Observable<any> {
    let logged = new Observable((observer) => {
      this.authService
        .signIn(GoogleLoginProvider.PROVIDER_ID)
        .then((data) => {
          this.isLoggedIn = true;
          console.log(this.isLoggedIn, 'form the service');
          observer.next(data);
        })
        .catch((observer) => {
          observer.error('error');
        });
    });

    return logged;
  }
}
